import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Checkbox,
  message,
  DatePicker
} from 'antd'
import style from './style/Form.scss'
import {CsxSelect, CsxSearch, CsxMultipleSearch} from 'Form'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {API_GETTASKSTATUS, API_GETTASKTYPE, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'
const {RangePicker} = DatePicker
import moment from 'moment'

@connect(
  state => ({...state.taskList, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd').startOf('day')
    update({searchParams: {initDate: [preWeek, moment().endOf('day')]}})
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    console.log(values)
    update({searchParams: {...searchParams, ...values}})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    // //为满足后台需要的参数进行处理：   未全量拣货-1，全量拣货-0 转换为 未全量拣货-0，全量拣货-1
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }

  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {...{}, allPickingFlag: ''}})
    // 重置时间
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        allPickingFlag,
        orderCode,
        productCode,
        productName,
        status,
        taskType,
        initDate,
        receivedBy,
        receivedByFlag
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label>订单查询：</label>
              <div className='box'>
                <Input placeholder='支持各种单号查询' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_GETTASKSTATUS}
              label='状态'
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              label='商品编码'
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              searchParamsKey='productCode'
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                console.log(value)
                this.onInputChange({productCode: value})
              }}

            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_GETTASKTYPE}
              label='任务类型'
              selectItem={{data: taskType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({taskType: value})
              }}
            />
          </Col>
          <Col span={4}>
            <Checkbox
              style={{marginLeft: 30}}
              checked={allPickingFlag}
              onChange={(e) => {
                this.onInputChange({allPickingFlag: e.target.checked})
              }}>
              未全量拣货
            </Checkbox>
          </Col>
          <Col span={4}>
            <Checkbox
              style={{marginLeft: 30}}
              checked={receivedByFlag}
              onChange={(e) => {
                this.onInputChange({receivedByFlag: e.target.checked})
              }}>
              领取人
            </Checkbox>
          </Col>
          <Col span={6}>
            <div className='commonInput'>
              <label>任务领取人：</label>
              <div className='box'>
                <Input value={receivedBy} onChange={(e) => {
                  this.onInputChange({receivedBy: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={10}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker
                  ranges={{当前时间: [moment('00:00:00', 'HH:mm:ss'), moment()]}}
                  showTime={{
                    defaultValue: [moment('00:00:00', 'HH:mm:ss'), moment('23:59:59', 'HH:mm:ss')]
                  }}
                  onChange={(mo, string) => {
                    this.onInputChange({initDate: mo})
                  }}
                  format={'YYYY-MM-DD HH:mm:ss'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <Row
              type="flex"
              justify="end"
            >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



