import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.taskList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className='blue' onClick={() => {
          this.onEdit(text, record, index)
        }}>
          {record.status == 1 && record.taskType != 1 && record.taskType != 6 ? '修改数量' : null}
        </span>
      </div>
    }, {
      title: '任务ID',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode'
    }, {
      title: '任务类型',
      width: 100,
      dataIndex: 'taskTypeCn',
      key: 'taskTypeCn'
    }, {
      title: '状态',
      width: 100,
      dataIndex: 'statusCn',
      key: 'statusCn'
    }, {
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '计划数量',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '拣货数量',
      width: 100,
      dataIndex: 'pickingQty',
      key: 'pickingQty'
    }, {
      title: '从库区',
      width: 100,
      dataIndex: 'fromReservoirAreaCode',
      key: 'fromReservoirAreaCode'
    }, {
      title: '从储位',
      width: 100,
      dataIndex: 'fromStoreLocationCode',
      key: 'fromStoreLocationCode'
    }, {
      title: '至库区',
      width: 100,
      dataIndex: 'toReservoirAreaCode',
      key: 'toReservoirAreaCode'
    }, {
      title: '至储位',
      width: 100,
      dataIndex: 'toStoreLocationCode',
      key: 'toStoreLocationCode'
    }, {
      title: '门店',
      width: 100,
      dataIndex: 'shopName',
      key: 'shopName'
    }, {
      title: '来源单号',
      width: 150,
      dataIndex: 'sourceCode',
      key: 'sourceCode'
    }, {
      title: '波次号',
      width: 150,
      dataIndex: 'waveCode',
      key: 'waveCode'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '完成时间',
      width: 150,
      dataIndex: 'finishTime',
      key: 'finishTime'
    }, {
      title: '修改人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '领取人',
      width: 100,
      dataIndex: 'receivedBy',
      key: 'receivedBy'
    }]
  }

  onEdit(text, record) {
    const {update, getSingleDet} = this.props
    // update({modalData: record, isShowModal: true, modalTitle: '修改拣货数量'})

    const {id = ''} = record
    getSingleDet({urlParams: [id]}, (data) => {
      console.log('data is', data)
      update({
        modalData: data && data.result || {}, isShowModal: true,
        tableLoading: false,
        modalTitle: '修改拣货数量'})
    })
  }

  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      selectedRowKeys,
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props

    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    // rowSelection object indicates the need for row selection
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys,
      getCheckboxProps: record =>
        ({
          disabled: record.status == 1 || record.taskType == 6
        })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          rowSelection={rowSelection}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



