import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.taskList}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }

  componentDidMount() {
    const {searchParams} = this.props
    for (let i in searchParams) {
      console.log(Object.prototype.toString.call(searchParams[i]))
    }
  }

  // 执行任务
  handleCarried = () => {
    const {handleGetList, selectedRowKeys, searchParams, page, size, handlePerTasks, update} = this.props
    // let reqObj = { ...searchParams, allPickingFlag: searchParams.allPickingFlag ? 0 : 1 }
    console.log(searchParams)
    if (!selectedRowKeys.length) {
      message.error('请勾选订单')
      return
    }
    handlePerTasks({data: {id: selectedRowKeys}}, () => {
      message.success('执行已完成')
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }

  // 导出功能
  handleExport=() => {
    const {handleAcitonExport, page, size, searchParams} = this.props
    // let reqObj = { ...searchParams, allPickingFlag: searchParams.allPickingFlag ? 0 : 1 }
    console.log(searchParams)
    handleAcitonExport({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }

  //解绑任务
  handleUnbunding=() => {
    const {selectedRows, handleUnbund} = this.props
    console.log(selectedRows)
    let array = []
    let paramArray = []
    selectedRows.map(o => array.push(o.receivedBy))
    selectedRows.map(o => paramArray.push(o.id))
    console.log(array.indexOf('') == -1, 222)
    if (!selectedRows.length) {
      message.error('请选择你要解绑的任务')
      return
    }
    if (array.indexOf('') == -1) {
      handleUnbund({data: {id: paramArray}}, () => {
        message.success('任务解绑完成')
        const {handleGetList, searchParams, size} = this.props
        // //为满足后台需要的参数进行处理：   未全量拣货-1，全量拣货-0 转换为 未全量拣货-0，全量拣货-1
        let reqObj = {...searchParams}
        handleGetList({searchParams: {...reqObj, page: 1, size}})
      })
    } else {
      message.error('有任务不存在领用人，请重新选择')
    }
  }

  render() {
    const {modalTuttonLoading, exportLoading, unbundingLoading} = this.props
    return (
      <div className={style['table-tool']}>
        <Row type="flex" justify="start">
          <Col span={12}>
            <Button type="primary" className={style.btn} loading={modalTuttonLoading} onClick={this.handleCarried}>执行任务</Button>
            <Button type="primary" className={style.btn} loading={exportLoading} onClick={this.handleExport}>导出</Button>
            <Button type="primary" className={style.btn} loading={unbundingLoading} onClick={this.handleUnbunding}>解绑任务</Button>
          </Col>
        </Row>
      </div>
    )
  }
}



