import * as actionType from './actionType'
import {getData} from 'util'
import {API_TASKS_PAGE, API_EXECUTETASK_CAEEIED, API_TASKS_EXPORT, API_UPDATETASKQTY_EDIT, API_TASKQTY_DET_GET, API_CANCLETASKRECEIVE} from 'api'
import moment from 'moment'
//同步
export const update = (object) => ({
  type: actionType.TASKLIST_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  delete reqObj.initDate
  getData({api: API_TASKS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断3
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.TASKLIST_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result && data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}

// // 执行任务
export const handlePerTasks = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE, data: {modalTuttonLoading: true, tableLoading: true}})
  let api = API_EXECUTETASK_CAEEIED
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.TASKLIST_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false,
        tableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}

// 导出功能
export const handleAcitonExport = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE, data: {exportLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD HH:mm:ss')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD HH:mm:ss')
  }
  delete reqObj.initDate
  getData({api: API_TASKS_EXPORT, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.TASKLIST_UPDATE,
      data: {
        exportLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}

//获取任务列表的详情
export const getSingleDet = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE})
  let api = API_TASKQTY_DET_GET
  getData({api, ...reqData}).then(data => {
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}
//修改任务列表
export const handleEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE})
  let api = API_UPDATETASKQTY_EDIT
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.TASKLIST_UPDATE,
      data: {
        isShowModal: false,
        tableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}

//解绑任务
export const handleUnbund = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.TASKLIST_UPDATE, data: {unbundingLoading: true}})
  let api = API_CANCLETASKRECEIVE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.TASKLIST_UPDATE,
      data: {
        unbundingLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.TASKLIST_FILE
    })
  })
}



