import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  InputNumber,
  Form,
  message,
  Button
} from 'antd'
import {popupFormItemLayout} from 'enum'
import {CsxFormSelect} from 'Form'
import {CsxFormCascader} from 'Form'
import {API_SHIPPERS_SHIPPERTYPES, API_DISTRICE_LIST} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.taskList}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
  }
  componentDidMount() {
    const {modalData} = this.props
    console.log(modalData)
  }

  handleSubmit = (e) => {
    const {form, modalData, handleEdit, handleGetList, size, searchParams} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {}
        reqObj.pickedQty = modalData.pickingQty
        reqObj.pickingQty = values.pickingQty
        reqObj.productCode = modalData.productCode
        reqObj.sourceCode = modalData.sourceCode
        reqObj.taskOrderCode = modalData.orderCode
        reqObj.taskType = modalData.taskType
        reqObj.id = modalData.id
        reqObj.waveCode = modalData.id
        console.log('reqObj is', reqObj)
        handleEdit({data: {...reqObj}}, () => {
          handleGetList({searchParams: {...searchParams, page: 1, size}})
          message.success('编辑成功')
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }

  render() {
    const {
      modalTitle,
      form: {getFieldDecorator},
      modalTuttonLoading,
      modalData = {}
    } = this.props
    const popupFormItemLayout = {
      marginBottom: '20px',
      labelCol: {
        xs: {span: 24},
        sm: {span: 6}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 15}
      }
    }
    let maxProps = modalData.maxPickingQty ? modalData.maxPickingQty : ''
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        maskClosable = {false}
        footer={null}
        width={560}
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <div style={{marginBottom: '40px'}}>
            <Row>
              <Col span={10} offset={2}>任务单号：{modalData.orderCode}</Col>
              <Col span={10} offset={2}>任务类型：{modalData.taskTypeCn}</Col>
              <Col span={22} offset={2}>商品名称：{`${modalData.productCode}-${modalData.productName}`}</Col>
              <Col span={10} offset={2}>已拣货数量：{modalData.pickingQty}</Col>
              <Col span={10} offset={2}>最大拣货数量：{modalData.maxPickingQty}</Col>
            </Row>
          </div>
          <Row>
            <Col span={24}>
              <FormItem
                {...popupFormItemLayout}
                label="修改数量"
                hasFeedback
                shouldUpdate={true}
                validateFirst = {true}
              >
                {getFieldDecorator('pickingQty', {
                  initialValue: modalData.pickingQty,
                  rules: [{
                    required: true,
                    message: '请填写!'
                  },
                  {
                    pattern: /^[0-9]+(\.[0-9]{1,3})?$/,
                    message: '请填写正确的数量'
                  },
                  {
                    validator(rule, value) {
                      if (Number(value) > Number(modalData.maxPickingQty)) {
                        return Promise.reject('不能超过最大拣货数量')
                      }
                      return Promise.resolve()
                    }
                  }
                  ]
                })(
                  <InputNumber style={{width: '100%'}} />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



